import Vue from 'vue'
import Router from 'vue-router'
const _import = file => () =>
  import (`@/views/${file}.vue`)
Vue.use(Router)

const router = new Router({
  routes: [{
      path: '/:id',
      name: 'main',
      component: _import('Main')
    },
    {
      path: '/',
      component: {
        template:'<div>hello</div>'
      }
    },
  ]
});

export default router;



